import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import LandingIntro from './LandingIntro'
import ErrorText from '../../components/Typography/ErrorText'
import InputText from '../../components/Input/InputText'
import { useDispatch } from "react-redux";
import { fetchLoginAction } from "../../app/service/action/authAction";
import SelectBox from "../../components/Input/SelectBox";
import { DATA_OPTION_SCOPE } from "../../utils/dummyData";

const Login = () => {

    const INITIAL_LOGIN_OBJ = {
        password: "",
        username: "",
        scope: "Member"
    }
    const dispatch = useDispatch()
    const [loading, setLoading] = useState(false)
    const [errorMessage, setErrorMessage] = useState("")
    const [loginObj, setLoginObj] = useState(INITIAL_LOGIN_OBJ)

    const submitForm = (e) => {
        e.preventDefault()
        setErrorMessage("")

        if (loginObj.username.trim() === "") return setErrorMessage("Email is required! (use any value)")
        if (loginObj.password.trim() === "") return setErrorMessage("Password is required! (use any value)")
        else {
            setLoading(true)
            dispatch(fetchLoginAction(loginObj)).then(response => {
                if (response.result && response.responseData.code === 200) {
                    localStorage.setItem("token", response.result.session.access_token)
                    localStorage.setItem("scope", response.result.session.scope)
                    localStorage.setItem("userDetail", JSON.stringify(response.result.userDetail))
                    if (response.result.session.scope === "Administrator") {
                        window.location.href = '/app/welcome'
                    } else {
                        window.location.href = '/member'
                    }
                }
                setLoading(false)
            }).catch(error => {
                console.error(error)
                setLoading(false)
            })
        }
    }

    const updateFormValue = ({ updateType, value }) => {
        setErrorMessage("")
        setLoginObj({ ...loginObj, [updateType]: value })
    }

    return (
        <div className="min-h-screen bg-base-200 flex items-center">
            <div className="card mx-auto w-full max-w-5xl  shadow-xl">
                <div className="grid  md:grid-cols-2 grid-cols-1  bg-base-100 rounded-xl">
                    <div className=''>
                        <LandingIntro />
                    </div>
                    <div className='py-24 px-10'>
                        <h2 className='text-2xl font-semibold mb-2 text-center'>Login</h2>
                        <form onSubmit={(e) => submitForm(e)}>
                            <div className="mb-4">
                                <SelectBox labelTitle='Role' options={DATA_OPTION_SCOPE} updateType={'scope'}
                                    defaultValue={loginObj.scope} updateFormValue={updateFormValue} />
                                <InputText type="emailId" defaultValue={loginObj.username} updateType="username"
                                    containerStyle="mt-4" labelTitle="Email"
                                    updateFormValue={updateFormValue} />
                                <InputText defaultValue={loginObj.password} type="password" updateType="password"
                                    containerStyle="mt-4" labelTitle="Password"
                                    updateFormValue={updateFormValue} />
                            </div>
                            <div className='text-right text-primary'><Link to="/forgot-password"><span
                                className="text-sm  inline-block  hover:text-primary hover:underline hover:cursor-pointer transition duration-200">Lupa Password?</span></Link>
                            </div>
                            <ErrorText styleClass="mt-8">{errorMessage}</ErrorText>
                            <button type="submit"
                                className={"btn mt-2 w-full btn-primary" + (loading ? " loading" : "")}>Login
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login
