import Swal from "sweetalert2";

function getError(error) {
    let result = "";

    if (error.code === 'ERR_NETWORK') {
        return getErrorMessage(error.message)
    }

    const { status } = error.response;
    switch (status) {
        case undefined:
            result = "Error - code is undefined!";
            break;

        case 401:
            result = "Invalid Token!";
            localStorage.clear();
            window.location.reload()
            break;

        case 403:
            result = "Forbidden Access!";
            break;

        case 404:
            result = "Not Found!";
            break;

        case 500:
            result = "Internal server error!";
            break;
        default:
    }
    return Swal.fire({
        icon: 'info',
        title: 'Oops...',
        text: result,
    });
}

function getResponseError(data) {
    return Swal.fire({
        icon: 'error',
        title: 'Opss...',
        text: data,
        footer: "Please contact your system support team."
    })
}

function getErrorMessage(message) {
    return Swal.fire({
        icon: 'error',
        title: 'Opss...',
        text: message,
        footer: "Please contact your system support team."
    })
}

function errorAlert(message) {
    return Swal.fire({
        title: 'Opss...',
        text: message,
        icon: 'info',
    });
}

function successAlert(message) {
    return Swal.fire({
        title: 'Yeah...',
        text: message,
        icon: 'success',
    });
}

function warningAlert(message: any) {
    return Swal.fire({
        title: 'Opss...',
        text: message,
        icon: 'warning',
    });
}


export { errorAlert, getError, getErrorMessage, getResponseError, successAlert, warningAlert};
