import React, {useState} from 'react'
import InformationCircleIcon from '@heroicons/react/24/outline/InformationCircleIcon'


const SelectBox = (props) => {

    const {
        labelTitle,
        labelDescription,
        defaultValue,
        containerStyle,
        placeholder,
        labelStyle,
        options,
        updateType,
        updateFormValue
    } = props

    const [value, setValue] = useState(defaultValue || "")

    const updateValue = (newValue) => {
        updateFormValue({updateType, value: newValue})
        setValue(newValue)
    }


    return (
        <div className={`inline-block ${containerStyle}`}>
            <label className={`label  ${labelStyle}`}>
                <div className="label-text">{labelTitle}
                    {labelDescription &&
                        <div className="tooltip tooltip-right" data-tip={labelDescription}><InformationCircleIcon
                            className='w-4 h-4'/></div>}
                </div>
            </label>

            <select className="select select-bordered w-full text-center" value={value}
                    onChange={(e) => updateValue(e.target.value)}>
                <option value="">{placeholder}</option>

                {
                    options ? options.map((o, k) => {
                        return <option value={o.value} key={k}>{o.name}</option>
                    }) : ""
                }
            </select>
        </div>
    )
}

export default SelectBox
