import {apiBaseUrl} from "../apiBaseUrl";
import {REQUEST_LOGIN, REQUEST_LOGIN_FAILED, REQUEST_LOGIN_SUCCESS} from "../constant/authConstant";
import {getError, getResponseError} from "../helper/handlingError";
import {ResponseApiInterface} from "../../../utils/interface";


export const fetchLoginAction = ({username, password, scope}) => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_LOGIN,
        });
        try {
            const {data, status}: ResponseApiInterface = await apiBaseUrl({
                method: 'POST',
                data: {
                    username: username,
                    password: password,
                    grant_type: 'password',
                    scope: scope,
                },
                headers: {
                    'Content-type': 'application/x-www-form-urlencoded',
                    Authorization: `Basic ${process.env.REACT_APP_BASIC_TOKEN}`,
                },
                url: '/api/v2/auth/login',
            });

            if (data.responseData.code === 404) {
                dispatch({
                    type: REQUEST_LOGIN_FAILED,
                });
                return getResponseError(data.responseData.message);
            }
            if (status === 200 && data.responseData.code === 200) {
                dispatch({
                    type: REQUEST_LOGIN_SUCCESS,
                    payload: data,
                });
                return data;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_LOGIN_FAILED,
            });
            await getError(error);
        }
    };
};
